import {
  iosVhFix
} from './utils/ios-vh-fix';
import {
  initModals
} from './modules/modals/init-modals';
import {
  Form
} from './modules/form-validate/form';
import {
  initSlowScroll
} from './modules/slow-scroll/slow-scroll';
import {
  initVideo
} from './modules/setup-video/init-video';
import {
  initTabs
} from './modules/tabs/init-tabs';
import {
  initCoachesGallery
} from './modules/coaches-gallery/init-coaches-gallery';
import {
  initReviewsGallery
} from './modules/reviews-gallery/init-reviews-gallery';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    const form = new Form();
    window.form = form;
    form.init();
  });
  initSlowScroll();
  initVideo();
  initTabs();
  initCoachesGallery();
  initReviewsGallery();
});

